<template>
  <div class="container">
    <div class="tabs">
      <div :class="{active: active == 1}" @click="tab(1)">注册人员审核</div>
      <div :class="{active: active == 2}" @click="tab(2)">专家审核</div>
    </div>
    <el-table :data="user" style="width: 100%" v-show="active == 1">
      <el-table-column prop="name" label="姓名" ></el-table-column>
      <el-table-column prop="mobile" label="手机号" ></el-table-column>
      <el-table-column prop="gender" label="性别" ></el-table-column>
      <el-table-column prop="company" label="公司" ></el-table-column>
      <el-table-column prop="department" label="部门" ></el-table-column>
      <el-table-column prop="no" label="员工编号" ></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <span class="color">分配权限</span>
          <span>拒绝</span>
        </template>
      </el-table-column>
    </el-table>
    <el-table :data="user" style="width: 100%" v-show="active == 2">
      <el-table-column prop="name" label="姓名" ></el-table-column>
      <el-table-column prop="mobile" label="手机号" ></el-table-column>
      <el-table-column prop="gender" label="性别" ></el-table-column>
      <el-table-column prop="company" label="公司" ></el-table-column>
      <el-table-column prop="title" label="职称" ></el-table-column>
      <el-table-column prop="subject" label="专业" ></el-table-column>
      <el-table-column prop="workyear" label="年限" ></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <span class="color">确定</span>
          <span>拒绝</span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { applyUser , applyexpert } from '@/api/login'
export default {
  data () {
    return {
      active: 1,
      user: [],
      expert: []
    }
  },

  components: {},

  computed: {},

  mounted: function() {
    applyUser().then(res => {
      console.log(res)
      this.user = res;
    })
    applyexpert().then(res => {
      console.log(res)
      this.expert = res;
    })
  },

  methods: {
    tab(i) {
      if(i == this.active) return;
      this.active = i;
    }
  }
}
</script>

<style lang='scss' scoped>
.container {
  margin: 20px 50px;
  .tabs {
    display: flex;
    margin-bottom: 20px;
    div {
      margin-right: 10px;
      padding: 10px 4px;
      cursor: pointer;
      &.active {
        border-bottom: 2px solid #666;
      }
    }
  }
  
  .color {
    color: #FF7E00;
  }
}
</style>
<style lang='scss'>
.container {
  .el-table , .el-table th, .el-table tr {
    color: #333 ;
  }

}
</style>




